<template>
  <v-dialog
    v-model="isModalOpen"
    persistent
    max-width="800px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        color="blue darken-4"
        dark
        v-bind="attrs"
        v-on="on"
      >
        <slot />
      </v-btn>
    </template>
    <v-card class="pa-4">
      <v-form
        @submit.prevent="onSubmit"
        class="custom-form"
        ref="form"
      >
        <v-card-title>
          <span class="headline">{{ title }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <form-row>
              <template v-slot:key>
                <label
                  class="custom-form-label"
                  :for="'facility-type'"
                >
                  Facility Type
                </label>
              </template>
              <template v-slot:value>
                <v-row>
                  <v-col cols="12" md="5" lg="5">
                    <v-select
                      id="facility-type"
                      :items="items"
                      :rules="rules.facilityType"
                      dense
                      outlined
                    ></v-select>
                  </v-col>
                  <v-col cols="12" md="7" lg="7">
                    <p>
                      Point for finger type. There is no inventory management / member page, accounting calculation, listing / parking lot.
                    </p>
                  </v-col>
                </v-row>
              </template>
            </form-row>
            <form-row>
              <template v-slot:key>
                <label
                  class="custom-form-label"
                  :for="'facility-name'">
                  Facility name (for management)
                </label>
              </template>
              <template v-slot:value>
                <v-row>
                  <v-col cols="12" md="5" lg="5">
                    <v-text-field
                      id="facility-name"
                      :rules="rules.facilityName"
                      dense
                      outlined
                    ></v-text-field>
                  </v-col>
                </v-row>
              </template>
            </form-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red accent-4"
            dark
            @click="isModalOpen = false"
          >
            Cancel
          </v-btn>
          <v-btn
            type="submit"
            color="blue darken-4"
            dark
          >
            Save and create
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
export default {
  name: 'CreateFacilitySettingModal',
  components: { FormRow },
  data () {
    return {
      title: 'Creating a new facility',
      isModalOpen: false,
      items: [
        {
          text: 'TVS Facility',
          value: 1
        },
        {
          text: 'TVS 2 Facility',
          value: 2
        },
        {
          text: 'TVS 3 Facility',
          value: 3
        }
      ],
      rules: {
        facilityType: [
          v => !!v || 'Type is required'
        ],
        facilityName: [
          v => !!v || 'Name is required',
          v => (v && v.length >= 3) || 'Name must be more than 3 characters'
        ]
      }
    }
  },
  methods: {
    async onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }

      this.isModalOpen = false
      await this.$router.push({ path: '/facility/settings/12/edit' })
    }
  }
}
</script>

