<template>
  <base-facility>
    <template v-slot:title>{{ $route.name }}</template>
    <template v-slot:default>
      <v-card class="v-card-responsive-table pa-5" shaped tile >
        <v-card-title class="grey--text mt-n5" v-once>{{ tableTitle }}</v-card-title>
        <v-simple-table class="custom-table d-flex mt-n4">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                ID
              </th>
              <th class="text-left">
                Name
              </th>
              <th></th>
              <th class="text-left">
                Facilities (goods production)
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in facilities"
              :key="item.id"
            >
              <td>{{ item.id }}</td>
              <td class="font-weight-medium">{{ item.name }}</td>
              <td>
                <v-btn
                  color="primary"
                  small
                  link
                  :to="'settings/' + item.id + '/room-types'"
                >Room Type Settings</v-btn>
              </td>
              <td class="text-center">
                <v-btn
                  color="white"
                  elevation="2"
                  x-small
                  fab
                  :to="'/facility/settings/12/edit'"
                >
                  <v-icon color="primary">
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
        <div class="text-end mt-5">
          <create-facility-setting-modal>Create a new facility</create-facility-setting-modal>
        </div>
      </v-card>
    </template>
  </base-facility>
</template>

<script>
import BaseFacility from '@/components/Facilities/BaseFacility'
import CreateFacilitySettingModal from '@/components/Facilities/Settings/CreateModal'

export default {
  name: 'FacilitySettings',
  data () {
    return {
      tableTitle: 'Facility Settings',
      facilities: []
    }
  },
  mounted () {
    this.initFacilities()
  },
  methods: {
    initFacilities () {
      for (let i = 0; i < 5; i++) {
        this.facilities.push({
          id: Math.floor(Math.random() * 90000) + 10000,
          name: 'Facility ' + (i + 1)
        })
      }
    }
  },
  components: {
    CreateFacilitySettingModal,
    BaseFacility
  }
}
</script>

<style scoped lang="scss">

</style>
